body {
  padding: 30px;
}

.container {
  margin: 0 auto;
}

header {
  padding: 40px;
  font-size: 33px;
  text-align: center;
  font-weight: bold;
}

.usercontainer{
  margin: 20px;
  float: left;
  text-align: center;
}

img {
  width: 250px;
}

.img {
  width: 250px;
}


.name {
  text-transform: uppercase;
  font-weight: bold;
}

.name span {
  border-bottom: 1px solid #24A059;
}

.email {
  text-transform: uppercase;
  font-weight: lighter;
  color: lightgrey;

}

.address {
  text-transform: capitalize;
  color: #767676;
}

.number {
  color: #767676;
}

.ss {
  color: #279727;
  -webkit-filter: blur(3px);
  filter: blur(3px);
  transition: filter 0 ease;

  &:hover {
    -webkit-filter: blur(0);
    filter: blur(0);
  }
}
